.footer {
  width: 100%;
  height: 260px;
  position: relative;
  color: #fff;
  background-color: #aba3e0; }
  .footer .content_container {
    height: 260px;
    font-size: 24px; }
    .footer .content_container .caption {
      width: calc(100% - 300px);
      float: left;
      height: 100%;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-align: center;
      align-items: center; }
    .footer .content_container .links {
      width: 300px;
      float: left;
      height: 100%;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-align: center;
      align-items: center; }
      .footer .content_container .links ul {
        margin: 0px; }
      .footer .content_container .links ul > li {
        list-style-type: none;
        padding: 5px 0px; }
  .footer .footnote {
    width: 100%;
    bottom: 0px;
    padding: 10px 30px;
    position: absolute;
    font-size: 16px; }



