.jb-loading {
  position: relative;
  text-align: center;
  width: 100px;
  margin-left: auto;
  margin-right: auto; }
  .jb-loading .jb-loading-dot {
    display: inline-block;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    margin-right: 3px;
    background: #17C9BB;
    -webkit-animation: wave 1.3s ease infinite;
    animation: wave 1.3s ease infinite; }
  .jb-loading .jb-loading-dot:nth-child(2) {
    -webkit-animation-delay: -1.1s;
    animation-delay: -1.1s;
    background: #FFF0BF; }
  .jb-loading .jb-loading-dot:nth-child(3) {
    -webkit-animation-delay: -0.9s;
    animation-delay: -0.9s;
    background: #FF7831; }

@-webkit-keyframes wave {
  0%,
  60%,
  100% {
    -webkit-transform: initial;
    transform: initial; }
  30% {
    -webkit-transform: translateY(-10px);
    transform: translateY(-10px); } }

@keyframes wave {
  0%,
  60%,
  100% {
    -webkit-transform: initial;
    transform: initial; }
  30% {
    -webkit-transform: translateY(-10px);
    transform: translateY(-10px); } }



